import React from 'react'

import ArticleListItem from "../ArticleListItem"
import BlogMenu from '../BlogMenu';
import MapArgentina from "../MapArgentina"
import Argentina from "../Argentina"
import Footer from "../Footer"

class ArgentinaComponent extends React.Component {  

  componentDidMount(){
    this.page.focus()
  }

   render() {
    return (
      <div>
      <BlogMenu />
     
       <div className="note">
          <div class="logo_text">
          <h3 tabIndex="-1" ref={(page) => { this.page = page}}>Ксения - путешественница</h3>
              <h4>Истории о моих путешествиях</h4>
          </div>
        <div className="country_grid">
        <div>  <Argentina /> </div> 
        <div><MapArgentina /></div>
        </div>
     </div>
     <Footer />
     </div>
    )
   }

  }


export default ArgentinaComponent