import React from 'react'


import BlogMenu from '../BlogMenu';
import MapMexico from "../MapMexico"
import Mexico from "../Mexico"
import Footer from "../Footer"

class MexicoComponent extends React.Component {  

  componentDidMount(){
    this.page.focus()
  }

   render() {
    return (
      <div>
      <BlogMenu />
     
       <div className="note">
          <div class="logo_text">
               <h3 tabIndex="-1" ref={(page) => { this.page = page}}>Ксения - путешественница</h3>
              <h4>Истории о моих путешествиях</h4>
          </div>
        <div className="country_grid">
        <div>  <Mexico /> </div> 
        <div><MapMexico /></div>
        </div>
     </div>
     <Footer />
     </div>
    )
   }

  }


export default MexicoComponent